body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Jost", sans-serif;
  font-size: 14px;
  line-height: 2;
  font-weight: normal;
  font-style: normal;
}
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  margin: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
main {
  padding: 0 10px;
}
@media (min-width: 760px) {
  main {
    padding: 0 20px;
  }
}
@media (min-width: 1024px) {
  main {
    padding: 0 40px;
  }
}
.portfolio {
  border-bottom: 1px solid #d8d8d8;
  padding-bottom: 20px;
}
.portfolio__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
.portfolio__item {
  margin: 0;
  padding: 0;
}
@media (min-width: 760px) {
  .portfolio__list {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1024px) {
  .portfolio {
    margin-bottom: 40px;
  }
  .portfolio__list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.card {
  display: block;
  box-shadow: 4px 4px 4px 3px #d8d8d8;
  border-radius: 2px;
  background-color: #FFFFFF;
}
.card:hover {
  box-shadow: 4px 4px 4px 3px #817d79;
}
.card__img-wrapper {
  width: 100%;
  height: 145px;
  overflow: hidden;
  padding: 0;
  box-sizing: border-box;
  display: block;
  appearance: none;
  border: none;
}
.card__img {
  width: 100%;
  display: block;
  margin: 0 auto;
}
.card__img:hover {
  transform: scale(1.2);
  transition: ease 0.5s;
}
.card__title {
  padding: 0 10px;
  margin: 0;
  font-size: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card__link-wrapper {
  display: flex;
  justify-content: space-around;
}
.card__link {
  width: auto;
  display: block;
  font-size: 15px;
  padding: 10px;
  text-align: center;
  margin: 20px 5px;
  color: black;
  background-color: #56ff50;
  text-decoration: none;
  box-shadow: 2px 2px 3px 0px #817d79;
}
.card__link :hover,
.card__link :active,
.card__link :focus {
  box-shadow: none;
  opacity: 0.9;
}
.card__link--code {
  background-color: #d8d8d8;
}
.card__text,
.card__but-close {
  display: none;
}
.card--active {
  z-index: 10;
  position: fixed;
  width: 80vw;
  max-height: 80vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow-y: auto;
  box-shadow: none;
}
.card--active:hover {
  box-shadow: none;
}
.card--active .card__img:hover {
  transform: none;
}
.card--active .card__link-wrapper {
  justify-content: flex-start;
}
.card--active .card__img-wrapper {
  height: 50%;
}
.card--active .card__text {
  display: block;
  padding: 10px;
}
.card--active .card__but-close {
  width: 50px;
  height: 50px;
  padding: 0;
  box-sizing: border-box;
  appearance: none;
  border: none;
  position: fixed;
  top: 0;
  right: 0;
  display: block;
  margin-right: 0;
  margin-left: auto;
  background-color: transparent;
  outline: 1px solid block;
}
.card--active .card__but-close::before,
.card--active .card__but-close::after {
  content: " ";
  position: absolute;
  top: 24px;
  right: 5px;
  width: 40px;
  height: 5px;
  background-color: #817d79;
}
.card--active .card__but-close::before {
  transform: rotate(45deg);
}
.card--active .card__but-close::after {
  transform: rotate(-45deg);
}
.card__bg-black {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
}
.card__bg-black--active {
  display: block;
}
.page-header {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.page-header__but-menu {
  order: 1;
  width: 50px;
  height: 50px;
  padding: 0;
  box-sizing: border-box;
  display: block;
  appearance: none;
  border: none;
  position: relative;
}
.page-header__but-menu--open {
  background-color: #56ff50;
}
.page-header__but-menu--open:after {
  position: absolute;
  content: "";
  width: 22.5px;
  height: 2.5px;
  left: 13.75px;
  top: 17.5px;
  background: #DADADA;
  box-shadow: 0 6px 0 0 #DADADA, 0 12px 0 0 #DADADA;
}
.page-header__but-menu--close {
  margin-right: 0;
  margin-left: auto;
  background-color: #FFFFFF;
  outline: 1px solid block;
}
.page-header__but-menu--close::before,
.page-header__but-menu--close::after {
  content: " ";
  position: absolute;
  top: 24px;
  right: 5px;
  width: 40px;
  height: 2px;
  background-color: #D7D9DF;
}
.page-header__but-menu--close::before {
  transform: rotate(45deg);
}
.page-header__but-menu--close::after {
  transform: rotate(-45deg);
}
.page-header__but-menu--no-js {
  display: none;
}
.page-header__menu {
  order: 2;
  margin: 0;
  margin-top: 26px;
  margin-bottom: 10px;
}
.page-header__menu--close {
  display: none;
}
.page-header__menu--no-js {
  display: block;
}
@media (min-width: 1024px) {
  .page-header {
    border-bottom: 1px solid #d8d8d8;
  }
  .page-header__but-menu {
    display: none;
  }
  .page-header__menu {
    margin: 0 auto;
    margin-bottom: 9px;
  }
}
.site-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.site-list__item {
  margin: 0;
  padding: 0;
}
.site-list__link {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  text-transform: uppercase;
  color: black;
  background-color: #d8d8d8;
  display: block;
  box-sizing: border-box;
  width: 100%;
  min-height: 57px;
  padding: 18px 16px;
  padding-right: 52px;
  margin-bottom: 20px;
}
@media (min-width: 1024px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    width: auto;
    padding: 0 35px;
    box-sizing: border-box;
    width: 100%;
  }
  .site-list__link {
    box-sizing: border-box;
    border: 1px solid transparent;
    background: none;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 2px;
    margin: 0;
    padding: 20px 10px;
  }
  .site-list__link:hover {
    background-color: #56ff50;
  }
}
.contacts {
  display: flex;
  flex-direction: column;
  margin: 0;
  border-bottom: 1px solid #d8d8d8;
  padding-bottom: 20px;
}
.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.contacts__photo {
  display: block;
}
.contacts__greeting {
  max-width: 650px;
}
@media (min-width: 760px) {
  .contacts {
    flex-direction: row;
  }
  .contacts__description-wrapper {
    margin-left: 40px;
  }
}
.contact {
  display: flex;
}
.contact__title,
.contact__link {
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: black;
  text-decoration: none;
}
.contact__link {
  margin-left: 10px;
}
.contact__link:hover {
  color: blue;
  text-decoration: underline;
}
.skills {
  border-bottom: 1px solid #d8d8d8;
  padding-bottom: 20px;
}
.skills__list-wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 760px) {
  .skills__list-wrapper {
    flex-direction: row;
    justify-content: space-around;
  }
}
.page-footer {
  display: block;
  min-height: 30px;
  background-color: #d8d8d8;
  color: black;
  line-height: 30px;
  font-size: 10px;
  text-align: center;
  vertical-align: middle;
}
.education {
  border-bottom: 1px solid #d8d8d8;
  padding-bottom: 20px;
}
.experience {
  border-bottom: 1px solid #d8d8d8;
  padding-bottom: 20px;
}
